













import { Component, Prop, Vue } from 'vue-property-decorator'

@Component
export default class GetName extends Vue {
  @Prop() private msg?: string;
  private name: string = '';
  private showNull: boolean = false;

  private submit() {
    if (this.name && this.name.trim()) {
      this.showNull = false;
       alert("Bạn tên là: " + this.name.trim());
    }
    else {
      this.showNull = true;
    }
  }
}
